<template>
	<div class="sub-container login">
		<h2 class="txt-center marB-50">로그인</h2>

		<div class="input-box">
			<div class="user-input">
				<input
					type="text"
					id="user-id"
					v-model="id"
					placeholder="아이디"
					autocomplete="off"
					@keyup.enter="login"
				/>

				<span id="check_id"></span>
			</div>
			<div class="user-input">
				<input
					type="password"
					id="user-pwd"
					name="pw"
					v-model="pw"
					placeholder="비밀번호"
					@keyup.enter="login"
				/>
				<span class="msg" id="check_pwd"></span>
			</div>
			<button class="btn full dark" id="login-btn" @click="login">
				로그인
			</button>
		</div>

		<div class="find-box">
			<div class="find-account-info">
				<p @click="goto('/find-id')">아이디 찾기</p>
				<p style="cursor: auto">|</p>
				<p @click="goto('/find-password')">비밀번호 찾기</p>
			</div>
		</div>

		<LoadingSpinner :loading="isLoading" />

		<div class="join-box">
			<p>처음 방문하셨다면</p>
			<button class="btn full" @click="goto('/join')">
				회원가입
			</button>
		</div>
	</div>
</template>

<script>
import { login } from '@/api/user';
import {
	saveAuthToCookie,
	saveAdminToCookie,
	saveIdToCookie,
	saveIdentityToCookie,
} from '@/utils/cookies';
import LoadingSpinner from '@/components/common/LoadingSpinner';
export default {
	name: 'Login',
	components: { LoadingSpinner },
	data() {
		return {
			id: '',
			pw: '',
			msg: '',
			isLoading: false,
		};
	},
	methods: {
		goto(url) {
			this.$router.push({ path: url });
		},
		async login() {
			this.isLoading = true;
			try {
				let userInfo = {
					identity: this.id,
					password: this.pw,
				};

				const result = await login(userInfo);
				if (result.data['jwt-token']) {
					this.$store.commit('LOGIN', result.data);
					saveAuthToCookie(result.data['jwt-token']);
					saveIdToCookie(result.data.id);
					saveAdminToCookie(result.data.adminYn);
					saveIdentityToCookie(result.data.identity);
					this.$router.push('/');
				} else {
					alert('아이디 혹은 비밀번호가 일치하지 않습니다');
				}
			} catch (e) {
				console.log(e);
				alert('아이디 혹은 비밀번호가 일치하지 않습니다');
			} finally {
				this.isLoading = false;
			}
		},
		initForm() {
			this.id = '';
			this.pw = '';
			this.msg = '';
		},
	},
};
</script>

<style scoped>
.find-box p {
	cursor: pointer;
}
.find-account-info {
	display: flex;
	justify-content: center;
}
.find-account-info > p {
	text-align: center;
	color: #bebebe;
	margin-right: 16px;
	margin-bottom: 10px;
	line-height: 25px;
}
</style>
